.App {
    flex: 1;
    color: #313131;
    line-height: 1.4;
    font-family: "Noto Serif", serif;
}

header nav {
    max-width: 1524px;
    display: flex;
    justify-content: right;
    padding-top: 16px;
    padding-right: 16px;
}

header nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}

header nav ul li {
    margin-right: 20px; /* Spacing between menu items */
}

header nav ul li:last-child {
    margin-right: 0; /* No margin to the right of the last item */
}

header nav ul li a {
    text-decoration: none; /* Remove the underline from links */
    color: #333333; /* Color of the links */
    font-weight: bold; /* Make the links bold */
}

header nav ul li a:hover {
    color: #099539;
}

/* Mobile styling */
.top-part {
    display: flex;
    flex-direction: column;
    margin: 32px auto 0;
    padding: 20px;
}

.top-part-text {
    padding-left: 32px;
    padding-right: 32px;
    text-align: justify;
    flex-direction: column;
    align-self: center;
}

.top-part h1 {
    margin-top: 0;
}

.profile-picture {
    max-width: 60%;
    height: auto;
    display: flex;
    padding-bottom: 24px;
    align-self: center;
}

/* Desktop styles with media query */
@media (min-width: 768px) { /* Adjust breakpoint as needed for desktop */
    .top-part {
        max-width: 1024px;
        flex-direction: row; /* Image and text in a row for desktop */
        align-items: center; /* Align items to the start of the cross axis */
    }

    .profile-picture {
        width: auto;
        max-width: 25%;
    }

    .top-part-text {
        margin-left: 20px; /* Add margin between image and text on desktop */
        width: auto; /* Allow text container to size according to content */
    }
}

/* style for text items that are stretched to fit maximum width */
.separator-list {
    display: flex;
    color: #099539;
    font-weight: bold;
    font-size: 14px;
    justify-content: space-between; /* This will distribute space evenly between the items */
    width: 100%; /* Ensure the container stretches to the full width */
}

.separator-list span {
}

.separator-list span:not(:last-child)::after {
    content: "|";
    margin: 0 10px; /* Adjust spacing around the separator as needed */
}

.projects-title {
    margin-top: 64px;
    text-align: center;
}

.App-link {
    color: #099539;
}